import React, { useState } from "react";
import {
  Box,
  Grid,
  FilledInput,
  Select,
  MenuItem,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Typography,
  makeStyles,
  Button,
  IconButton,
  CircularProgress,
} from "@material-ui/core";
import { Close as CloseIcon } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  skillChip: {
    margin: theme.spacing(0.5),
    padding: theme.spacing(0.75),
    fontSize: "14.5px",
    borderRadius: "5px",
    transition: ".3s",
    fontWeight: "600",
    border: `1px solid ${theme.palette.secondary.main}`,
    color: "#000",
    cursor: "pointer",

    "&:hover": {
      backgroundColor: theme.palette.secondary.main,
      color: "#fff",
    },
  },
  includes: {
    backgroundColor: theme.palette.secondary.main,
    color: "#fff",
  },
}));

const initState = {
  title: "",
  type: "Full time",
  companyName: "",
  companyUrl: "",
  location: "Remote",
  link: "",
  description: "",
  skills: [],
};

export default (props) => {
  const classess = useStyles();
  const [loading, setLoading] = useState(false);
  const [jobDetails, setJobDetails] = useState(initState);

  const handleChange = (e) => {
    e.persist();
    setJobDetails((oldState) => ({
      ...oldState,
      [e.target.name]: e.target.value,
    }));
  };

  const handleSubmit = async () => {
    for (const field in jobDetails) {
      if (typeof jobDetails[field] == "string" && !jobDetails[field]) return;
    }
    if (!jobDetails.skills.length) return;
    setLoading(true);
    await props.postJob(jobDetails);
    closeModal();
    // for(const field in jobDetails){
    //  if(typeOfjobDetails[field]);
    // }
    // setJobDetails(initState);
    // await props.postJob(jobDetails);
    // closeModal();
  };

  const closeModal = () => {
    setJobDetails(initState);
    setLoading(false);
    props.closeModal();
  };

  const addRemoveSkill = (skill) =>
    jobDetails.skills.includes(skill)
      ? setJobDetails((oldState) => ({
          ...oldState,
          skills: oldState.skills.filter((s) => s !== skill),
        }))
      : setJobDetails((oldState) => ({
          ...oldState,
          skills: oldState.skills.concat(skill),
        }));

  const skills = [
    "JavaScript",
    "React js",
    "Node js",
    "Vue",
    "MongoDB",
    "Express",
    "SQL",
  ];
  // console.log(jobDetails);
  return (
    <Dialog open={props.newJobModal} fullWidth>
      <DialogTitle>
        <Box display="flex" justifyContent="space-between" alignItems="center">
          Post Job
          <IconButton onClick={closeModal}>
            <CloseIcon />
          </IconButton>
        </Box>
      </DialogTitle>

      <DialogContent>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <FilledInput
              required
              name="title"
              value={jobDetails.title}
              onChange={handleChange}
              placeholder="job title *"
              disableUnderline
              fullWidth
            />
          </Grid>
          <Grid item xs={6}>
            <Select
              required
              name="type"
              value={jobDetails.type}
              onChange={handleChange}
              disableUnderline
              variant="filled"
              fullWidth
            >
              <MenuItem value="Full time"> Full time</MenuItem>
              <MenuItem value="Part time"> Part time</MenuItem>
              <MenuItem value="Contract"> Contract</MenuItem>
            </Select>
          </Grid>

          <Grid item xs={6}>
            <FilledInput
              required
              onChange={handleChange}
              name="companyName"
              value={jobDetails.companyName}
              placeholder="Company name*"
              disableUnderline
              fullWidth
            />
          </Grid>

          <Grid item xs={6}>
            <FilledInput
              required
              onChange={handleChange}
              name="companyUrl"
              value={jobDetails.companyUrl}
              placeholder="Comapny URL *"
              disableUnderline
              fullWidth
            />
          </Grid>

          <Grid item xs={6}>
            <Select
              required
              onChange={handleChange}
              name="location"
              value={jobDetails.location}
              disableUnderline
              variant="filled"
              fullWidth
            >
              <MenuItem value="Remote">Remote</MenuItem>
              <MenuItem value="In-Office">In-Office</MenuItem>
            </Select>
          </Grid>

          <Grid item xs={6}>
            <FilledInput
              required
              onChange={handleChange}
              name="link"
              value={jobDetails.link}
              placeholder="Job link *"
              disableUnderline
              fullWidth
            />
          </Grid>

          <Grid item xs={12}>
            <FilledInput
              required
              onChange={handleChange}
              name="description"
              value={jobDetails.description}
              placeholder="Job Description *"
              disableUnderline
              fullWidth
              multiline
              rows="4"
            />
          </Grid>
        </Grid>

        <Box mt={2}>
          <Typography>Skills</Typography>
          <Box display="flex" required>
            {skills.map((skill) => (
              <Box
                onClick={() => addRemoveSkill(skill)}
                className={`${classess.skillChip}
               ${jobDetails.skills.includes(skill) && classess.includes}`}
                key={skill}
              >
                {skill}
              </Box>
            ))}
          </Box>
        </Box>
      </DialogContent>

      <DialogActions>
        <Box
          color="red"
          width="100%"
          display="flex"
          justifyContent="space-between"
          alignItems="center"
        >
          <Typography variant="caption"> * Required Fields</Typography>
          
          <Button
            onClick={handleSubmit}
            variant="contained"
            color="primary"
            disableElevation
            disabled={loading}
          >
            {loading ? (
              <CircularProgress color="primary" size={22} />
            ) : (
              "Post Job"
            )}
          </Button>
        </Box>
      </DialogActions>
    </Dialog>
  );
};
